#filter {
  max-width: 1200px;
  margin: 0 auto;
  height: 30px;
  width: 300px;
  padding: 0 15px;
  margin: 30px 0;
  border: 5px solid #005165;
  border-radius: 20px;
}

.filter-text{
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-style: italic;
  padding: 10px 0;
}

.technologies {
  min-height: 100px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 1200px;
  margin: 10px auto;
  justify-content: center;

  &-filter {
    height: 10vh;
    margin: 20px;
    filter: grayscale(100%);
    // transition: all 0.3s ease-in;
    cursor: pointer;
    transition: transform 0.3s;

    &:hover {
      // transition: transform 0.3s;
      // transform: rotate(10deg)
      transform: scale(1.2);

    }
  }
}


img.active-tech {
  // border: 10px solid red;
  filter: grayscale(0);
  transform: scale(1.2);
  // animation-duration: .3s;
  transition: transform 0.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
  // animation-name: clickTech;
}

