@media (min-width: 0) and (max-width:800px) {
  #hidden {
    display: none;
  }

}

@media (min-width: 801px) {
  .hidden {
    display: none;
  }

  .nav-list {
    margin-right: 50px;
  }

  .nav-item {
    margin: 1vh 1vw;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.2s linear;

    &:hover {
      transform: scale(1.1);
      // background-color: #151515;
      // text-decoration: underline;
      // font-style: italic;
    }
  }
}

.header {
  padding: 0 3vw;
  min-height: 60px;
  max-width: 1200px;
  margin:  0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  &-block {
    display: flex;
    align-items: center;
  }

  &-button__image {
    height: 30px;
    // filter: invert(78%) sepia(15%) saturate(6618%) hue-rotate(358deg) brightness(106%) contrast(105%);
  }

  &__burger {
    &-list {
      right: 0;
      left: 0;
      margin: 0;
      top: 0;
      position: fixed;
      padding: 0 0 0 6vw;
      padding-top: 30vh;
      height: 100%;
      flex-direction: column;
      z-index: 9999;
      list-style: none;
      background-color: #005165;
    }

    &-item {
      font-size: 10vw;
      // color: #ffc000;
    }
  }

  &__link {
    margin-right: 1vw;
    // filter: invert(10%) sepia(98%) saturate(4124%) hue-rotate(180deg) brightness(100%) contrast(102%);
    // filter: invert(78%) sepia(15%) saturate(6618%) hue-rotate(358deg) brightness(106%) contrast(105%);

    &:last-child {
      margin-right: 0;
    }
  }

  &__icon {
    height: 25px;
    width: 25px;
    border-radius: 100%;
    transition: all 0.3s;

    &:hover {
      transform: scale(1.3);
    }
  }
}

.logo {
  &-image {
    height: 40px;
    transition: all .3s;

    &:hover {
      transform: rotate(360deg) scale(1.3);
    }
  }
}

.nav {
  &-list {
    padding: 0;
    display: flex;
    list-style: none;
    // font-size: 20px;
    // color: #ffc000;
  }
}

.burger__image {
  cursor: pointer;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }
}

.span-item {
  cursor: pointer;
  margin: 1vh 1vw;
  text-transform: uppercase;
  transition: all 0.2s linear;

  &:hover {
    padding-left: 20px;
  }
}