@media (min-width: 0) and (max-width: 600px) {
  .project {
    flex-direction: column;
    margin: 20px !important;
  }

  .project__image-small {
    min-height: 270px;
    width: 100%;
    margin: 20px 0;
    transition: transform 0.5s, background-size 0.5s;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    border-radius: 10px;
    max-width: 560px;
  }

  .project__image-mobile {
    height: 0;
  }
}


@media (min-width: 601px) and (max-width: 1000px) {
  .project__image-small {
    height: 0;
  }

  .project {
    flex-direction: column !important;
    padding: 0;
    width: 80vw;

    &__description {
      margin-bottom: 10px;
    }

    &__description,
    &__example {
      width: 100%;
    }
  }

  .half {
    height: 100%;
    // margin: 20px 0;
  }


  .project__image-mobile {
    // height: 200px;
    min-height: 270px;
    width: 100%;
    margin: 20px 0;
    transition: transform .5s, background-size 0.5s;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    border-radius: 10px;
    max-width: 560px;
  }

  h5 {
    margin: 10px;
  }

}

@media (min-width: 1001px) {
  .half {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .project {
    // width: 70vw;
    flex-direction: column;

    margin: 40px auto;

    &__image {
      height: 300px;
    }

    &__description {
      width: 40vw;
      flex: 1;
    }

    &__example {
      flex: 1;
      min-width: 200px;
      min-height: 300px;
      max-height: 300px;
      margin: 0;
      width: 60vw;
      min-width: 200px;
    }
  }

  .row {
    margin-right: 40px;
  }

  .row-reverse {
    margin-left: 40px;
  }
}

.project {
  background-color: rgba(240, 239, 239, 0.741);
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 10px;
  position: relative;
  z-index: 2;
  display: flex;
  margin: 0 auto 40px;
  box-sizing: border-box;
  max-height: 100%;
  justify-content: center;
  align-items: center;
  max-width: 1200px;

  &__title {
    font-size: 1.8rem;
    margin: 10px;
    // color: #ffc000
  }

  &__text {
    font-size: 1rem;
    overflow: hidden;
    padding: 0 30px;
  }

  &__description {
    box-sizing: border-box;
    flex-direction: column;
    padding: 30px 0;
    text-align: left;
    flex-grow: 1;
    font-family: 'Noto Sans', sans-serif;
    min-width: 100px;
    border-radius: 10px;
    // color: rgb(250, 250, 250);
    background-color: white;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.03);
    }
  }

  &__example {
    flex: 1;
  }

  &__image {
    min-height: 100%;
    width: 100%;
    transition: transform .5s, background-size 0.5s;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    border-radius: 10px;
    // max-width: 560px;

    &:hover {
      transform: scale(1.05);
    }
  }

  &__technologies-list {
    &-block {
      display: flex;
      justify-content: center;
    }
  }

  &__buttons {
    display: flex;
    font-family: 'Urbanist', sans-serif;
    font-weight: 300;
    margin: 20px 0;
    font-size: 1rem;
  }

  &__title {
    font-family: 'Urbanist', sans-serif;
    font-weight: 400;
  }
}

.half {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  // max-width: 560px;
}

.button {
  width: 100px;
  padding: 5px;
  margin: 5px;
  text-align: center;
  transition: border 0.2s;
  position: relative;
  transform: perspective(1px) translateZ(0);
  transition: 0.4s;

  &:before {
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background: black;
    clip-path: inset(0 100% 0 0);
    opacity: 0.2;
    transition: 0.4s;
  }

  &:hover::before {
    clip-path: inset(0 0 0 0);
    height: 100%;
    width: 100%;
  }
}


a {
  text-decoration: none
}

.row {
  // background-color: #005165;
  color: black;
}

.row-reverse {
  // background-color: #ffc000;
  color: black;
}

.technologies-image {
  height: 20px;
  width: 20px;
}