@import url('https://fonts.googleapis.com/css2?family=Signika+Negative:wght@300;400;700&family=Urbanist:wght@100;300&display=swap');

@media (min-width: 0) and (max-width: 450px) {
  .profile-container {
    flex-direction: column;
  }
}

@media (min-width: 0) and (max-width: 700px) {
  div.name {
    font-size: 6vh;
  }

  .hiddenOnMobile {
    display: none;
  }

  div.visibleOnMobile {
    display: block;
  }

  div.greeting,
  div.job {
    font-size: 1.5rem;
  }
}

.profile-container {
  display: flex;
  padding: 3.5vh 3vw;
  position: relative;
  // height: 50vh;
  overflow: hidden;
  margin: 0 auto;
  justify-content: space-around;
  // align-items: center;
  max-width: 1200px;
}

.content {
  // margin: 3.5vh 2vw;
  margin-right: 2vw;
  z-index: 1;
}

.greeting,
.job {
  // color: white;
  font-size: 2rem;
  // position: absolute;
  z-index: 100;
  right: 5vw;
  bottom: 5vh;
  // text-align: right;
  font-family: 'Signika Negative', sans-serif;
}

.name {
  color: #ffc000;
  font-size: 5rem;
  // position: absolute;
  z-index: 100;
  right: 5vw;
  bottom: 5vh;
  // text-align: right;
  font-family: 'Signika Negative', sans-serif;
}

.about {
  margin-top: 10px;
  font-size: 1.3rem;
  font-family: "Signika Negative", sans-serif;
}

.project__links,
.project__links:active,
.project__links:visited {
  color: rgb(83, 83, 83);
  text-shadow: none;
  z-index: 100;
  text-decoration: none;
}

.links {
  position: absolute;
  z-index: 100;
  top: 5vh;
  right: 5vw;
  display: flex;
  justify-content: space-between;
  font-family: 'Urbanist', sans-serif;
  font-size: 1.2rem;
}

.project__links:hover {
  color: #EEF1FF;
  // text-shadow: 0px 0px 25px #ffc000;
}

.email {
  margin-left: 0.5rem;
}


svg {
  width: 100%;
  height: 100%;
}

.circle {
  width: min(100vw, 190vh);
  height: min(95vw, 190vh);
  background-color: #ffc000;
  border-radius: 100%;
  margin: 0vh 0 0 -50vw;
  position: absolute;
  left: 0;
  top: -5vh;
  z-index: 1;
  animation: circle 90s infinite linear;
}

@keyframes circle {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media(min-width: 0) and (max-width:800px) {
  .profile {
    flex-direction: column;

    &-picture {
      justify-content: center;
      margin-top: 20px;
    }
  }

  .profile-image {
    width: 50%;
  }
}

@media(min-width: 800px) {
  .profile {
    flex-direction: row;

    &-image {
      height: 20vh;
    }
  }
}

.profile {
  width: 70vw;
  display: flex;
  justify-content: space-around;
  margin: 50px auto 30px;
  color: #ffc000;
  align-items: center;
  max-width: 1200px;
  // background-color: #005165;

  &-block {
    flex-direction: column;
    padding: 0 50px;
    font-size: 1rem;
    line-height: 2;
    align-items: flex-start;
  }

  &-block,
  &-picture {
    display: flex;
    // align-items: center;
    padding: 10px;
  }

  &-image {
    // border: 5px solid #ffc000;
    height: 200px;
    width: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}