@media(min-width: 0) and (max-width:800px) {
  .description {
    flex-direction: column;

    &-picture {
      justify-content: center;
      margin-top: 20px;
    }
  }

  .description-image {
    width: 50%;
  }
}

@media(min-width: 800px) {
  .description {
    flex-direction: row;

    &-image {
      height: 20vh;
    }
  }
}

.description {
  width: 70vw;
  display: flex;
  justify-content: space-around;
  margin: 50px auto 30px;
  color: #ffc000;
  align-items: center;
  max-width: 1200px;
  // background-color: #005165;

  &-block {
    flex-direction: column;
    padding: 0 50px;
    font-size: 1rem;
    line-height: 2;
    align-items: flex-start;
  }

  &-block,
  &-picture {
    display: flex;
    // align-items: center;
    padding: 10px;
  }

  &-image {
    // border: 5px solid #ffc000;
    height: 300px;
    width: 300px;
    background-repeat: no-repeat;
    background-size: cover;
  }
}